	},
	onShow() {
		var _this = this;
		uni.getStorage({
			key:'cutMoney',
			success(res) {
				console.log(res.data);
				_this.cutMoney = res.data.money;
				_this.cutId = res.data.id;
			}
		})
		
		if(this.status==1){
			uni.request({
				url: this.url + 'home/index/get_transportation_order',
				method:'POST',
				data:{
					tid: _this.orderId
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						var order = res.data.list;
						_this.order = order;
						_this.status = order.status;
					}
				},
				fail(error) {
					console.log(error)
				}
			})
		} else {
			uni.request({
				url: this.url + 'home/user/user_order_id',
				method:'POST',
				data:{
					id: _this.orderId
				},
				success(res) {
					//console.log(res);
					if(res.data.code==100){
						var order = res.data.msg;
						_this.order = order;
						_this.outPhoto = order.start_image_url;
						_this.inPhoto = order.end_image_url;
						if(_this.outPhoto!=null && _this.inPhoto==null){
							_this.uploadPhoenStep = 2;
						} else if(_this.inPhoto!=null) {
							_this.uploadPhoenStep = 3;
						}
						
						_this.status = order.states;
						
						if(_this.status==7){
							uni.showModal({
								title:'提示',
								content:'订单已确认，是否立即支付？',
								success(res) {
									if(res.confirm){
										_this.togglePopup("carShipper");
									}
								}
							})
						}
						
						if(order.coupon_money!=null){
							_this.cutMoney = order.coupon_money;
						}
						_this.lookInfoNeedMoney = order.total_fee;
					}
				},
				fail(error) {
					console.log(error)
				}
			})
		}
		
	},
	methods: {
		takeToken() {	//接单
			var _this = this;
			
			uni.request({
				url: this.url + 'home/Order/driver_add_order',
				method:'POST',
				data:{
					user_id: _this.user_id,
					tran_id: _this.orderId
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						uni.showModal({
							content: "接单成功，请耐心等待货主确认。",
							confirmText: "确定",
							showCancel:false
						})
					} else if(res.data.code==200) {
						uni.showToast({
							title:"等待货主确认中，请不要重复接单",
							mask:true,
							icon:"none",
							duration:2000
						})
					}
				},
				fail(error) {
					console.log(error)
				}
			})
			
		},
		changeCar(value) {
			this.carNum = value;
		},
		togglePopup(type) {
			this.type = type;
		},
		checkboxChange(e) {
			if(e.detail.value[0]=="agree"){
				this.isAgree = e.detail.value[0];
			} else {
				this.isAgree ='';
			}
		},
		isSure() {
			if(this.isAgree == "agree"){
				//支付
				uni.showLoading({
					title:'请求支付中...',
					mask:true
				})
				var _this = this;
				this.togglePopup('');
				
				uni.request({
					url: this.url + 'home/wxpay/wx_pay',
					method:'POST',
					data:{
						user_id: _this.user_id,
						user_order_id: _this.orderId,
						coupon_id:_this.cutId
					},
					success(res) {
						console.log(res);
						if(res.data.code==100){
							uni.hideLoading();
							var data = res.data.msg;
							uni.requestPayment({
								provider:"wxpay",
								timeStamp:data.timeStamp.toString(),
								/* timeStamp:new Date().getTime().toString(), */
								nonceStr:data.nonceStr,
								package:data.package,
								signType:data.signType,
								paySign:data.paySign,
								success(paySuccess){
									console.log(paySuccess);
									_this.status = 6;
									uni.showToast({
										title:'支付成功',
										icon:"success",
										mask:true
									})
									uni.request({
										url: _this.url + 'home/Order/update_transportation_order',
										method:'POST',
										data:{
											id: _this.orderId,
											states: 6
										},
										success(res) {
											console.log(res);
											/* if(res.data.code==100){
												uni.hideLoading();
												_this.status = 4;
												_this.uploadPhoenStep = step;
												_this.uploadSuccess = 0;
												_this.uploadPhoto = '';
												uni.showToast({
													title:'修改成功',
													icon:"success",
													mask:true
												})
											} */
										},
										fail(error) {
											console.log(error);
										}
									})
								},
								fail(payError) {
									console.log(payError);
									uni.showToast({
										title:"支付失败",
										icon:"none",
										mask:true
									})
								}
							});
						}
					},
					fail(error) {
						console.log(error)
					}
				})
				
			}
		},
		selectYhq() {
			uni.navigateTo({
				url:'../selectCoupon/selectCoupon'
			})
		},
		goAppeal(){
			if(this.status!=5){	//5
				uni.navigateTo({
					url:'../appeal/appeal'
				})
			}
		},
		nextStep(step) {
			var _this = this;
			if(_this.uploadSuccess==1){
				uni.showLoading({
					title:'修改中...',
					mask:true
				})
				uni.getLocation({
					success: function (res) {
						console.log(res);
						var let_lon = res.longitude+','+res.latitude;
						console.log(let_lon);
						if(step==2){
							uni.request({
								url: _this.url + 'home/Order/update_transportation_order',
								method:'POST',
								data:{
									id: _this.orderId,
									states: 4,
									start_image_url :_this.outPhoto,
									let_lon: let_lon
								},
								success(res) {
									console.log(res);
									if(res.data.code==100){
										uni.hideLoading();
										_this.status = 4;
										_this.uploadPhoenStep = step;
										_this.uploadSuccess = 0;
										_this.uploadPhoto = '';
										uni.showToast({
											title:'修改成功',
											icon:"success",
											mask:true
										})
									}
								},
								fail(error) {
									console.log(error);
									uni.showToast({
										title:'修改失败',
										icon:"none",
										mask:true
									})
								}
							})
						}else if(step==3){
							uni.request({
								url: _this.url + 'home/Order/update_transportation_order',
								method:'POST',
								data:{
									id: _this.orderId,
									/* states: 5, */
									end_image_url :_this.inPhoto,
									let_lon: let_lon
								},
								success(res) {
									console.log(res);
									if(res.data.code==100){
										uni.hideLoading();
										/* _this.status = 5; */
										_this.uploadPhoenStep = step;
										_this.uploadSuccess = 0;
										_this.uploadPhoto = '';
										uni.showToast({
											title:'修改成功',
											icon:"success",
											mask:true
										})
									}
								},
								fail(error) {
									console.log(error);
									uni.showToast({
										title:'修改失败',
										icon:"none",
										mask:true
									})
								}
							})
						}
					},
					fail(error) {
						console.log(error);
						uni.hideLoading();
					}
				});
				
			}
		},
		uploadImg() {
			var _this = this;
			uni.chooseImage({
				count:1,
				sourceType:["camera"],
				success(res) {
					var imgPath = res.tempFilePaths[0];
					uni.showLoading({
						title: '上传中...'
					});
					uni.uploadFile({
						url: _this.url + 'index/index/uploadImage',
						filePath: imgPath,
						name: 'filePath',
						formData: {
							'uid': _this.userinfo.user_id
						},
						success: (uploadFileRes) => {
							uni.hideLoading();
							if(uploadFileRes.statusCode==200){
								var file = JSON.parse(uploadFileRes.data);
								if(file.code==100){
									_this.uploadSuccess = 1;
									if(_this.uploadPhoenStep==1){
										_this.outPhoto = file.url;
									} else if(_this.uploadPhoenStep==2){
										_this.inPhoto = file.url;
									}
									_this.uploadPhoto = imgPath;
									console.log("上传图片成功");
								}
							}
						},
						fail() {
							console.log('上传失败')
						}
					});
					
					console.log("选择图片成功");
				},
				fail() {
					console.log("选择图片失败");
				},
				complete() {
					console.log("选择图片完成");
				}
			})
		},
		go() {	//出发装货
			var _this = this;
			// 修改司机的运单状态
			uni.request({
				url: _this.url + 'home/Order/update_transportation_order',
				method:'POST',
				data:{
					id: _this.orderId,
					states: 4
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						_this.status = 4;
					}
				},
				fail(error) {
					console.log(error)
				}
			})
			// 修改货主的运单状态
			uni.request({
				url: _this.url + 'home/release/update_information',
				method:'POST',
				data:{
					id:_this.order.tran_id,
					states: 4
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						_this.status = 4;
					}
				},
				fail(error) {
					console.log(error)
				}
			})
		},
		sureCancel() {	//取消订单
			var _this = this;
			// 修改司机的运单状态
			uni.request({
				url: _this.url + 'home/Order/update_transportation_order',
				method:'POST',
				data:{
					id: _this.orderId,
					states: 1
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						_this.status = 2;
						uni.showToast({
							title:'你已取消该订单',
							icon:"success",
							mask:true
						});
						_this.togglePopup('');
					}
				},
				fail(error) {
					console.log(error)
				}
			})
		}
	}
}
